.listOutils {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 1em;
    margin-bottom: 2em;
    margin-top: 2em;
    justify-content: center;
}

.sizeLogo{
    
    height:3em;
}

.containerDescription {
    
    margin-top: -50%;
    background-color: var(--primaire-color);
    animation: animateContainer 1.6s forwards 1.4s;
    border-radius: 0em 0em 0.4em 0.4em;
    padding: 4em 0em 3em 0em;
    width: 100%;

}

.animateProjet {
    animation: animateProject 1.6s forwards;

}
.description{
    margin: 1em;
}

.btnPos {
    display: flex;
    justify-content: center;
}

.btnLink {
    padding: 1em 2em 1em 2em;
    border-radius: 2.3em;
    font-size: 1.4em;
    background-color: var(--secondaire-color);
    color: aliceblue;
    opacity: 0;
    border: none;
    animation: btnAnimate 1s forwards 2s;

}

.containerCardAll{
    width: 800px;
}

@keyframes animateProject {


    0% {
        margin: 0;
        transform: scale(0);
    }

    100% {
        transform: scale(1);
        margin-top: 5em;
        margin-bottom: 5em;
    }
}



@keyframes animateContainer {


    0% {

        transform: scaleY(0.5);
    }

    100% {
        transform: scaleY(1);
        margin-top: -2em;
    }
}




@keyframes btnAnimate {
    0% {
        opacity: 0;
        transform: scale(0.3);
    }

    50% {
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }

}


@media screen and (max-width: 991px) {
    .projet {
        margin-top: 0em;
        margin-bottom: 3em;
    }
@keyframes animateProject {


